@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Ubuntu:wght@300&display=swap');

:root {
    --red: rgb(173, 3, 3);
}

* {
    margin: 0;
}

body {
    font-family: 'Poppins', sans-serif;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 1280px;
    height: 72px;
}

nav ul {
    display: flex;
    list-style: none;
    gap: 34px;
}

nav ul li {
    cursor: pointer;
}

nav ul li:hover {
    color: var(--red);
    transition: 0.5s;
    transform: scale(1.1);
}

button {
    display: flex;
    align-items: right;
    all: unset;
    padding: 6px 16px;
    background: var(--red);
    height: 32px;
    color: white;
    font-weight: 500;
}

button:hover {
    cursor: pointer;
    transition: 0.5s;
    transform: scale(1.1);
}

.container {
    margin: 0 auto;
    align-items: center;
}

.main {
    display: flex;
    max-width: 1280px;
    /* margin: 0 auto;
    /* align-items: center;  */
    height: calc(100vh-72px);
}

.main-content {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.main-content h1 {
    font-weight: 800;
    line-height: 102px;
    font-size: 108px;
}

.main-content p {
    font-weight: 600;
    color: black;
    max-width: 400px;
}

.mainbtn {
    display: flex;
    gap: 40px;
}

.secondary-btn {
    background: transparent;
    color: black;
    border: 1px solid black;
}

.brandicon {
    display: flex;
    gap: 16px;
}



@media(max-width: 700px) {
    .main-content {
        display: flex;
        flex-direction: column;
        gap: 6px;
    }

    .main-content h1 {
        font-weight: 800;
        line-height: 60px;
        font-size: 60px;
    }

    .main-content p {
        font-weight: 600;
        max-width: 200px;
    }

    .mainbtn {
        display: flex;
        gap: 20px;
    }

    .brandicon {
        display: flex;
        gap: 8px;
    }

    .main-img img {
        height: 300px;
        width: 300px;
    }
}

@media(min-width: 701px) and (max-width: 1030px) {
    .main-content {
        display: flex;
        flex-direction: column;
        gap: 6px;
    }

    .main-content h1 {
        font-weight: 800;
        line-height: 70px;
        font-size: 70px;
    }

    .main-content p {
        font-weight: 600;
        max-width: 300px;
    }

    .mainbtn {
        display: flex;
        gap: 20px;
    }

    .brandicon {
        display: flex;
        gap: 8px;
    }

    .main-img img {
        height: 400px;
        width: 400px;
    }

}